<template>
    <div class="common-infinity-scroll" >
        <div class="scroll">
           <span class="content">{{ msg }}</span>
            <span class="content-copy">{{ msg }}</span> 
        </div>
        
    </div>
</template>
<script>
export default {
    name: 'InfinityScroll',
    props: {
        msg: {
            type: String,
            default: ''
        }
    }
}
</script>
<style scoped>
    .common-infinity-scroll{
        width: 300px;
        height: 40px;
        border: 1px solid red;
        overflow: hidden;
        display: flex;
        
    }
    .scroll {
        display: flex;
        animation: inScroll Linear 10s infinite;
    }
    .scroll:hover {
        animation-play-state: paused;
    }
    .content{
        display: inline-block;
        line-height: 40px;
        white-space: nowrap;
        /* transition: transform 3s ease; */
        /* animation: inScroll Linear 10s infinite; */
    }

    .content-copy {
        color: blue;
        width: 300px;
        height: 100%;
        display: inline-block;
        line-height: 40px;
        white-space: nowrap;
        /* transition: transform 3s ease; */
        /* animation: inScroll Linear 10s infinite; */
    }

    @keyframes inScroll {
        from {
            transform: translateX(0px);
        }

        to {
            transform: translateX(calc(-100% + 300px));
        }
    }
</style>